import React from 'react';
import CookiesImg from '../../images/cookies.png';

type CookiesProps = {
  hideCookies: () => void;
};

const Cookies: React.FC<CookiesProps> = ({ hideCookies }) => {
  return (
    <div className="cookies">
      <div className="cookies__text">
        <img src={CookiesImg} alt="cookies" />
        <p className="text">
          Ta strona uywa plikw Cookies. Jeli bdziesz kontynuowa przegldanie tej witryny, zaoymy, e akceptujesz
          <a href="/polityka-prywatnosci"> polityk prywatnoci i cookies.</a>
        </p>
      </div>
      <button className="btn" onClick={() => hideCookies()}>
        TAK, AKCEPTUJ
      </button>
    </div>
  );
};

export default Cookies;
